.blog {
    &__list {
        .row {
            > .col-xs-12 {
                display: flex;
            }
        }
    }

    &__head {
        position: relative;
        background: $white;
        margin-top: -60px;
        box-shadow: 0 2px 4px 0 rgba(206, 206, 206, 0.5);
        margin-bottom: calcRem(38px);

        .blog {
            &__image {
                overflow: hidden;
            }

            &__btn {
                position: absolute;
                top: calcRem(10px);
                left: calcRem(30px);

                .btn {
                    font-size: calcRem(12px);
                    letter-spacing: 2.8px;
                    border-radius: 9px;
                    text-transform: uppercase;
                    min-width: auto;
                    padding: calcRem(12px) calcRem(25px);
                }
            }
        }
    }

    &__heading {
        color: $dark-gray;
        @include fontSize(18px);
        line-height: 1.94;
        letter-spacing: 3.1px;
        margin-bottom: 20px;
    }

    &__image {
        img {
            width: 100%;
            max-width: 100%;
        }
    }

    &__card {
        margin-bottom: 25px;
        flex: 1;
        display: flex;
        flex-direction: column;
    }

    &__content {
        padding: calcRem(45px);
        background: $white;
        flex: 1;
        display: flex;
        flex-direction: column;
    }

    &__title {
        @include fontSize(26px);
        line-height: 1.46;
        letter-spacing: 5.5px;
        text-transform: uppercase;
        color: #212529;
    }

    &__tags {
        margin-bottom: 15px;
        display: flex;
        gap: 5px;
        flex-wrap: wrap;

        span {
            background: $dark-gray;
            color: $white;
            border-radius: 3px;
            @include fontSize(12px);
            line-height: 1.46;
            letter-spacing: 5.5px;
            font-family: $gotham;
            text-transform: uppercase;
            padding: 5px 25px;
        }
    }

    &__btn {
        text-align: right;
        margin-top: 30px;

        .fa {
            margin-left: 15px;
        }
    }

    &__text {
        flex: 1;
    }

    @include media-breakpoint-down(md) {
        &__head {
            margin-top: 80px;
        }
        &__content {
            padding: calcRem(25px);
        }
        &__description {
            padding: 15px;
        }
    }
}