
.client {
    .wp-block-petland-care-client-story {
        display: flex;

        &:nth-child(even) {
            .client {
                &__chat {
                    background: $white;

                    &:after {
                        border-top: 40px solid $white;
                    }
                }

                &__date,
                &__message {
                    color: $dark;
                }
            }
        }
    }

    &__image {
        margin-bottom: 5px;

        img {
            display: block;
            width: 100%;
        }
    }

    &__chat {
        position: relative;
        background: $dark-gray;
        padding: calcRem(40px);
        margin-bottom: 50px;
        flex: 1;

        &:after {
            content: '';
            position: absolute;
            top: 100%;
            right: 50px;
            width: 0;
            height: 0;
            border-top: 40px solid $dark-gray;
            border-left: 40px solid transparent;
        }
    }

    &__date,
    &__message {
        color: $white;
        font-family: $gotham;
        @include fontSize(14px);
    }

    &__message {
        font-weight: 300;
        @include fontSize(20px);
        line-height: 1.5;
        letter-spacing: 0.6px;
    }

    &__info {
        text-align: right;
    }

    &__name,
    &__job {
        font-family: $gotham;
        @include fontSize(18px);
        line-height: 1.6;
        letter-spacing: 0.9px;
        margin: 0;
    }

    &__job {
        font-weight: 300;
    }

    &__card {
        display: flex;
        flex-direction: column;

        &--white {
            .client {
                &__chat {
                    background: $white;

                    &:after {
                        border-top: 40px solid $white;
                    }
                }

                &__date,
                &__message {
                    color: $dark;
                }
            }
        }
    }
}