.food {
    padding-bottom: 30px;

    &__card {
        margin-bottom: calcRem(40px);
    }

    &__image {
        img {
            display: block;
            width: 100%;
            max-width: 100%;
        }
    }

    &__title {
        background: $primary;
        padding: 2px 10px;

        h4 {
            @include fontSize(18px);
            color: $white;
            text-transform: uppercase;
            line-height: 1.94;
            letter-spacing: 1.5px;
            margin-bottom: 0;
        }
    }
}