.pagination {
    margin-bottom: 60px;

    .disabled {
        display: none;
    }

    .page-item {
        padding: 0 13px;
    }

    .page-link {
        width: 70px;
        height: 70px;
        background: $white;
        font-size: calcRem(23px);
        color: #404041;
        text-align: center;
        line-height: 2;
        border: 0;

        &:hover {
            background: $primary;
            color: $white;
        }
    }

    @include media-breakpoint-down(md) {
        .page-item {
            padding: 0 8px;
        }
    }
}