/**
 * Config Color
 */
$theme-colors: () !default;
$theme-colors: map-merge((
        "primary": $primary,
        "secondary": $secondary,
        "success": $green,
        "info": $info,
        "light": $white,
        "dark": $dark,
        "dark-gray": $dark-gray,
), $theme-colors);
$body-bg: #f1f1f1;