.nav {
  &__buttons {
    .nav {
      &-tabs {
        border: 0;
      }
      &-item {
        width: 50%;
        border: 0;
        background: #e6e6e6;
        text-align: center;
        font-family: $gotham;
        font-weight: 400;
        color: #8d8d8d;
        letter-spacing: 2.1px;
        &.active {
          background: transparent;
          color: $dark-gray;
        }
      }
    }
  }
  @include media-breakpoint-up(md) {
    &__buttons {
      .nav {
        &-tabs {
          border: 0;
        }
        &-item {
          padding-top: 28px;
          padding-bottom: 16px;
          font-family: $lato;
          font-weight: 900;
          letter-spacing: 1.7px;
          text-transform: uppercase;
          color: $dark-gray;
          @include transition(all ease-in 0.1s);
        }
      }
    }
  }
}
