//checkbox
.custom-checkbox {
    input {
        display: none;
    }

    label {
        font-family: $lato;
        font-weight: 900;
        @include fontSize(12px);
        color: $dark-gray;
        vertical-align: middle;
        cursor: pointer;

        &:before {
            content: '';
            display: inline-block;
            width: calcRem(21px);
            height: calcRem(18px);
            border-radius: 3px;
            border: solid 2px $dark-gray;
            background: transparent;
            margin-right: calcRem(17px);
            cursor: pointer;
            vertical-align: middle;
        }
    }

    input[type='checkbox'] {
        &:checked + label {
            &:before {
                background: $dark-gray;
            }
        }
    }
}