.icon {
    background: center no-repeat;
    background-size: contain;
    display: inline-block;

    &--gold-star {
        background-image: icon('gold-star.svg');
        height: 20px;
        width: 20px;
    }

    &--arrow-down {
        background-image: icon('icon-arrow-down.svg');
        height: 16px;
        width: 16px;
    }

    &--arrow-right {
        background-image: icon('icon-arrow-right.svg');
        height: 16px;
        width: 16px;
    }

    &--chevron-up {
        background-image: icon('icon-chevron-up.svg');
        height: 1.2rem;
        width: 1.2rem;
    }

    &--benefits {
        background-image: icon('icon-benefits.svg');
    }

    &--check-mark {
        background-image: icon('icon-check-mark.svg');
        height: 16px;
        width: 19px;
    }

    &--clock {
        background-image: icon('icon-clock.svg');
        height: 20px;
        width: 20px;
    }

    &--phone {
        background-image: icon('icon-phone.svg');
        height: 12px;
        width: 12px;
    }

    &--close-rounded {
        background-image: icon('icon-close-rounded.svg');
        height: 36.6px;
        width: 36.6px;
    }

    &--filter {
        background-image: icon('icon-filter.svg');
    }

    &--sort {
        background-image: icon('icon-sort.svg');
    }

    &--petland-logo {
        background-image: icon('icon-petland-logo.svg');
        height: 36px;
        width: 32px;
    }

    &--tik-tok {
        background-image: icon('icon-tiktok-white.svg');
    }

    &--arrow-down,
    &--arrow-right {
        height: 16px;
        width: 16px;
    }

    &--filter,
    &--benefits {
        width: 19px;
        height: 16px;
    }

}