// Buttons
.btn {
    border-radius: 1px;
    min-width: 253px;
    @include fontSize(20px);
    font-family: $gotham;
    font-weight: 300;
    letter-spacing: calcRem(1.7px);
    outline: 0 !important;
    box-shadow: none !important;

    &:hover {
        opacity: 1;
        background: $dark-gray;
        border-color: $dark-gray;
    }

    &-lg {
        padding: 1.2rem;
    }

    &-success {
        color: $white;
    }

    &--image {
        img {
            transform: translateX(-25px);
            max-width: 25px;
            max-height: 25px;
        }
    }

    &--update {
        border-radius: 4px;
    }

    &--dropdown {
        padding: 0 8px;
        @include fontSize(10px);
        color: $dark-gray;
        background: $white;
        width: 140px;
        min-width: auto;
        border-radius: 2px;
        opacity: 1;
        display: inline-flex;
        justify-content: space-between;
        text-transform: uppercase;

        &:hover {
            background: $white;
            opacity: 0.8;
        }

        .fa {
            font-size: 1rem;
        }
    }

    &--icon {
        min-width: 190px;
        @include fontSize(23px);
        font-family: $lato;
        font-weight: 100;
        line-height: 1.57;
        letter-spacing: 2.1px;
        padding: calcRem(23px);
        text-align: center;

        &.btn--update {
            align-items: center;
            display: flex;
            font-weight: 400;
            justify-content: space-between;
        }
    }

    &--accent {
        background: $dark-gray;
        color: white;

        &:hover {
            border-color: $primary;
            background: $primary;
            color: white;
        }
    }

    .less {
        display: inherit;
    }

    .more {
        display: none;
    }

    &.collapsed {
        .less {
            display: none;
        }

        .more {
            display: inherit;
        }
    }

    @include media-breakpoint-down(sm) {
        &-lg {
            width: 100%;
        }
        &--icon {
            min-width: 100%;
        }
    }
    @media only screen and (max-width: 320px) {
        min-width: 100%;
    }
}

.group {
    &__buttons {
        display: block;
        width: 100%;
        text-align: right;
        padding: calcRem(48px) 0 calcRem(58px);
    }

    @include media-breakpoint-down(md) {
        &__buttons {
            text-align: center;
        }
    }
}


//new button Design
.button {
    border: 0;
    border-radius: 4px;
    color: $light;
    display: inline-block;
    font-family: $gotham;
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.18;
    min-width: 189px;
    outline: none;
    padding: 16px 24px;
    text-align: center;

    &:hover {
        color: $light;
        opacity: .5;
        text-decoration: none;
    }

    &--primary {
        background: $primary;
    }

    @include media-breakpoint-down(sm) {
        display: block;
    }
}