.checkbox-list {
  padding: 0 16px;

  &__group {
    > input {
      display: none;

      &:checked ~ label {
        .checkbox-list__checkmark {
          background: $new-dark-gray;
          border-color: $new-dark-gray;

          &::before {
            display: block;
          }
        }
      }
    }

    > label {
      align-items: center;
      cursor: pointer;
      display: flex;
      margin-bottom: 8px;
      padding-bottom: 9px;
    }
  }

  &__label {
    font-family: $gotham;
    margin-left: 16px;
  }

  &__checkmark {
    display: block;
    border: 2px solid $black;
    border-radius: 2px;
    height: 18px;
    position: relative;
    width: 18px;

    &::before {
      border-bottom: 2px solid $white;
      border-right: 2px solid $white;
      content: '';
      display: none;
      height: 11px;
      left: 4px;
      position: absolute;
      transform: rotate(40deg);
      width: 7px;
    }
  }
}