body {
  font-family: $gotham;
  overflow-x: hidden;
  @include media-breakpoint-down(md) {
    padding-bottom: 60px;
    padding-top: 45px;
  }
}

a {
  @include transition(color ease-in 0.1s);
}

p {
  font-family: $gotham;
  font-weight: 300;
  line-height: 1.8;
  letter-spacing: 0.5px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $lato;
  font-weight: 900;
}

img {
  max-width: 100%;
}
