.inventory {
  &__wrapper {
    @include media-breakpoint-up(lg) {
      display: grid;
      gap: 40px;
      grid-template-columns: 260px auto;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(247px, 1fr));
    gap: 20px;
    height: fit-content;

    @include media-breakpoint-down(md) {
      .filters ~ & {
        margin-top: 100px;
      }
    }
  }
  .sort-by {
    grid-column: 1/-1;
  }
}