.form {
    &__title,
    &__subtitle {
        @include fontSize(30px);
        line-height: 1.49;
        letter-spacing: 4.3px;
        color: $dark-gray;
        text-transform: uppercase;
    }
    &__subtitle {
        @include fontSize(20px);
        line-height: 1.7;
    }
    &__text {
        @include fontSize(15px);
        line-height: 2.21;
        letter-spacing: 0.4px;
    }
    &__add {
        position: absolute;
        width: auto;
        height: 100%;
        right: 0;
        top: 0;
        padding: 7px;
        z-index: 9;
        text-align: center;
        &--item {
            cursor: pointer;
            background: $dark-gray;
            color: $white;
            display: block;
            width: 35px;
            height: 100%;
            line-height: 2.5;
            margin: 0;
        }
    }
    &-control {
        background: $white;
        border: 1px solid #e0e0e0;
        height: 59px;
        border-radius: 0;
        @include fontSize(18px);
        font-family: $lato;
        font-weight: 300;
        line-height: 2.38;
        letter-spacing: 1.6px;
        padding-left: 20px;
        padding-right: 20px;
        &[type='file'] {
            &:after {
                content: attr(data-name);
                position: absolute;
                top: 2px;
                left: 2px;
                background: #ffffff;
                color: #6c757d;
                width: calc(100% - 4px);
                z-index: 0;
                padding: 0.375rem 20px;
            }
            &::-webkit-file-upload-button {
                visibility: hidden;
                width: 0;
            }
        }
        &:focus {
            border-color: #c1e9eb;
            box-shadow: 0 0 0 0.2rem rgba(193, 233, 235, 0.25);
        }
        &.is-invalid {
            border: 2px solid $primary;
            font-weight: 900;
            &::placeholder {
                color: $primary;
            }
        }
    }
    &-group {
        position: relative;
        > label {
            display: none;
        }
        textarea.form-control {
            height: 140px;
        }
        .invalid-feedback {
            margin: 0;
            img {
                height: 23px;
                width: 23px;
                position: absolute;
                top: calc(50% - 13px);
                right: 20px;
            }
        }
    }
    .btn--icon {
        padding-top: 1rem;
        padding-bottom: 1rem;
        .fa {
            margin-left: 10px;
        }
    }
}