.detail {
    &__head {
        background: $white;
        padding: 20px;
        margin-top: -60px;
        position: relative;
        box-shadow: 0 2px 4px 0 rgba(206, 206, 206, 0.5);
        margin-bottom: calcRem(38px);

        &--fluid {
            padding: 0;
        }
    }

    &__image {
        position: relative;

        img {
            max-width: 100%;
            width: 100%;
        }
    }

    &__stock {
        position: absolute;
        top: 25px;
        right: 25px;
        border-radius: 11px;
        background: $dark-gray;
        padding: 5px 18px;
        color: $white;
        @include fontSize(10px);
        letter-spacing: 1.3px;
        text-transform: uppercase;
    }

    &__subtitle {
        padding-top: 20px;
        display: flex;
        align-items: baseline;
        padding-bottom: calcRem(10px);
        border-bottom: 3px solid $primary;

        h2 {
            @include fontSize(30px);
            font-family: $gotham;
            font-weight: 700;
            letter-spacing: 5px;
            color: $dark-gray;
            text-transform: uppercase;
            margin-left: 10px;

            span {
                margin-right: 5px;
            }
        }

        margin-bottom: 32px;

        &--lg {
            h2 {
                @include fontSize(42px);
            }

            @include media-breakpoint-up(md) {
                border-bottom: 0;
            }
        }
    }

    &__subtitle--similar {
        display: flex;
        align-items: baseline;
        padding-top: calcRem(20px);
        border-top: 3px solid $dark-gray;
        margin-top: 60px;

        h2 {
            @include fontSize(30px);
            font-family: 'Lato';
            font-size: 26.4px;
            font-weight: 900;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.18;
            letter-spacing: 4.6px;
            color: $dark-gray;
            text-transform: uppercase;

            span {
                margin-right: 5px;
            }
        }

        margin-bottom: 60px;
        @include media-breakpoint-up(md) {
            margin-bottom: 30px;
        }

        &--lg {
            h2 {
                @include fontSize(42px);
            }

            @include media-breakpoint-up(md) {
                border-bottom: 0;
            }
        }
    }

    &__description {
        max-height: 220px;
        overflow-y: auto;
        padding: 10px;

        &::-webkit-scrollbar {
            width: 6px !important;
            height: 6px !important;
        }

        &::-webkit-scrollbar-thumb {
            background-color: $dark-gray;
        }

        &::-webkit-scrollbar-track {
            background: #cecece;
        }

        p,
        h6 {
            line-height: 1.81;
            letter-spacing: 0.5px;
            margin-bottom: 1.5rem;
        }

        h6 {
            font-family: $gotham;
            font-weight: 700;
        }
    }

    &__bio {
        padding-right: calcRem(40px);

        &--v2 {
            padding-top: 25px;
            padding-right: 0;

            .detail {
                &__description {
                    max-height: 100%;
                    min-height: 350px;
                }
            }
        }
    }

    &__panel {
        padding: 0 25px 25px;
    }

    &__list {
        list-style: none;
    }

    &__item {
        > span {
            @include fontSize(14px);
            line-height: 35px;
            font-family: $lato;
            font-weight: 700;
        }

        .detail {
            &__span {
                min-width: 80px;
                display: inline-block;
                color: #404041;
            }

            &__detail {
                color: #404041;
                font-family: $lato;
                font-size: 14px;
                font-weight: 300;
                font-style: normal;
                font-stretch: normal;
                line-height: 2.5;
                letter-spacing: 0.4px;

                .color {
                    width: 14px;
                    height: 14px;
                    margin-right: 6px;
                    display: inline-block;
                    border: 1px solid black;
                }
            }
        }
    }

    &__button {

        .btn {
            min-width: 180px;
            font-size: 12px;
            font-weight: 500;
            line-height: 3.22;
            letter-spacing: 0.3px;

            img {
                transform: none;
                margin-right: 7px;
            }
        }

        @include media-breakpoint-up(sm) {
            &:not(:last-child) {
                margin-right: 7px;
            }
        }
    }

    &__reserve {
        .btn {
            font-size: 1rem;
            @include media-breakpoint-down(sm) {
                font-size: 0.8rem;
            }
        }
    }

    &__actions {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    .specs {
        margin-bottom: 30px;
        @include media-breakpoint-up(md) {
            display: block !important;
            opacity: 1;
        }

        .no-bottom {
            margin-bottom: 3px;
            margin-top: 27px;
        }

        &__title {
            font-family: $gotham;
            font-weight: 700;
            @include fontSize(24px);
            letter-spacing: 0.8px;
            line-height: 1.6;
            margin-bottom: 3rem;
        }

        &__text {
            @include fontSize(20px);
            letter-spacing: 0.5px;
            line-height: 2;
        }

        &__progress {
            margin-bottom: 1.7rem;

            .specs__title {
                margin-bottom: 1rem;
            }

            .no-bottom {
                margin-bottom: 14px;
                margin-top: 60px;
            }
        }
    }

    .progress {
        margin-bottom: 14px;
        background: $white;
        border: 1px solid #d1d1d1;
        padding: 5px;
        height: calcRem(28px);
        border-radius: 0;

        &__label {
            margin-bottom: 0;
            font-family: $gotham;
            font-weight: 400;
            @include fontSize(14px);
            line-height: 2.14;
            letter-spacing: 1.5px;
            color: $dark-gray;
        }
    }

    &__container {
        .why-petland {
            p {
                font-family: $gotham;
                font-size: 13px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.54;
                letter-spacing: 0.4px;
            }

            .has-medium-font-size {
                font-family: $gotham;
                font-size: 16px;
                font-weight: 500;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.5;
                letter-spacing: 1.6px;
                text-align: center;
                color: #45515d;
                text-transform: uppercase;
            }
        }
    }

    @include media-breakpoint-down(md) {
        .why-petland {
            .wp-block-columns {
                align-items: center;
                justify-content: center;
            }

            .wp-block-column {
                margin-right: 0;
                margin-left: 0;
            }
        }
        &__head {
            padding: 0;
            margin: 0;
            box-shadow: none;
        }
        &__title {
            @include fontSize(28px);
            font-family: $gotham;
            font-weight: 700;
            letter-spacing: 5.9px;
            color: $dark-gray;
            text-transform: uppercase;
            padding: 15px 35px;
            box-shadow: none;
            margin: 0;
        }
        &__container {
            padding: 0 30px;
        }
        &__bio {
            padding-right: 0;

            &--v2 {
                &.collapse:not(.show) {
                    display: none;
                }
            }
        }
        &__description {
            max-height: 100%;
            padding: 0;
        }
        &__subtitle {
            h2 {
                font-size: calcRem(28px) !important;
            }
        }
        &__panel {
            padding: 0;
        }
        &__button {
            padding: 10px 25px;
        }
        &__actions {
            display: block;
            width: 100%;
            margin-top: 25px;
        }
        .btn--collapse {
            background: $dark-gray;
            color: $white;
            position: relative;
            border: 0;
            @include transition(all ease-in 0.2s);
            font-family: $lato;
            @include fontSize(16px);
            font-weight: 900;
            letter-spacing: 2.6px;
            text-transform: uppercase;
            opacity: 1;
            text-align: left;
            padding-left: 60px;

            .fa {
                position: absolute;
                right: 35px;
                top: 7px;
                transform: rotate(0);
                @include transition(all ease-in 0.2s);
            }

            &.collapsed {
                background: #d8d8d8;
                color: $dark-gray;
                opacity: 0.5;

                .fa {
                    transform: rotate(-90deg);
                }
            }
        }
        .finance {
            &__content {
                margin-left: -30px;
                margin-right: -30px;
            }
        }
    }
    @media only screen and (max-width: 320px) {
        &__subtitle {
            h2 {
                font-size: 1rem !important;
            }
        }
    }
}