// Header
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 100vw;
  background: $white;
  @include transition(all ease-in-out 0.3s);
  z-index: 9999;
  &__row {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding-top: 1.35rem;
    padding-bottom: 1.75rem;
  }
  .mainNav {
    &__list {
      margin-bottom: 0;
      padding-left: 0;
    }
    &__item {
      display: inline-block;
      padding: 0 0.5vw;
      &:last-child {
        padding-right: 0;
      }
      a {
        display: flex;
        align-items: center;
        cursor: pointer;
        position: relative;
        font-family: $lato;
        font-weight: 900;
        text-transform: uppercase;
        color: $dark;
        @include fontSize(12px);
        letter-spacing: 0.2rem;
        &:after {
          content: '';
          background: $primary;
          position: absolute;
          bottom: -6px;
          left: 0;
          height: 3px;
          width: 100%;
          transform: scaleX(0);
          transform-origin: left;
          transition: all ease-in-out 0.3s;
        }
        &:hover {
          text-decoration: none;
        }
      }
      &:hover,
      .expand {
        > a:after {
          transform: scaleX(1);
        }
        &__submenu-arrow {
          transform: rotate(0);
        }
        .mainNav__sub {
          @include media-breakpoint-up(md) {
            transform: scaleY(1);
            opacity: 0.95;
          }
        }
      }
    }
    &__submenu-arrow {
      transform: rotate(180deg);
      transition: all ease-in-out .3s;
      @include media-breakpoint-up(md) {
        height: 14px;
        width: 14px;
      }
    }
    &__sub {
      background: $dark-gray;
      opacity: 0;
      position: absolute;
      padding: calcRem(30px);
      list-style: none;
      margin-bottom: 0;
      margin-top: 3px;
      transform: scaleY(0);
      transform-origin: top;
      z-index: 9;
      @include transition(all ease-in-out, 0.3s);
      li {
        padding-bottom: 0.8rem;
        &:last-child {
          padding-bottom: 0;
        }
        a {
          color: $white;
          @include fontSize(10px);
          letter-spacing: calcRem(1.5px);
          &:hover {
            color: $primary;
          }
        }
      }
    }
    &__close {
      position: absolute;
      bottom: 2rem;
      left: calc(50% - 23px);
      z-index: 99;
      width: 46px;
      height: 46px;
      a {
        width: 46px;
        height: 46px;
        border-radius: 50%;
        text-align: center;
        background: $white;
        color: $dark-gray;
        font-size: 48px;
        display: inline-block;
        line-height: 35px;
      }
    }
    //Main nav breakpoint sm
    @include media-breakpoint-down(md) {
      position: fixed;
      top: 0;
      left: 0;
      height: 100vh; // remove it when it has more browser support
      height: 100svh;
      width: 100%;
      visibility: hidden;
      opacity: 0;
      background: $white;
      padding: 2rem;
      overflow-y: auto;
      @include transition(all ease-in-out 0.3s);
      &.open {
        visibility: visible;
        opacity: 1;
      }
      &__list {
        text-align: left;
      }
      &__item {
        display: block;
        padding: 1% 0;
        a {
          font-family: $gotham;
          font-size: 18px;
          font-weight: 700;
          line-height: 1.84;
          letter-spacing: 6px;
          color: $dark !important;
          &:after {
            display: none;
          }
        }
        &.expand {
          > a {
            color: $primary !important;
          }
          .mainNav__sub {
            transform: scaleY(1);
            opacity: 1;
            max-height: 1000rem;
            visibility: visible;
          }
        }
      }
      &__sub {
        position: relative;
        background: $dark-gray !important;
        margin-top: 0;
        margin-inline: -3rem;
        left: 0;
        width: 100vw;
        max-height: 0;
        padding: 0 3rem;
        visibility: hidden;
        @include transition(all ease-in-out, 0.3s);
        li {
          &:first-child {
            padding-top: 1.5rem;
          }
          &:last-child {
            padding-bottom: 1.5rem;
          }
          a {
            @include fontSize(19px);
            letter-spacing: calcRem(2.8px);
            line-height: 1.58;
            color: $white !important;
            &:hover {
              color: $primary !important;
            }
          }
        }
      }
    }
    .social {
      padding-top: 1rem;
      &__item {
        margin: 0 0.1rem;
        &:first-child {
          margin-left: 0;
        }
        a .fa {
          color: white;
        }
      }
    }
    &__info {
      margin-top: 15px;
      a,
      p {
        font-family: $gotham;
        font-size: 16px;
        font-weight: 300;
        line-height: 2.69;
        letter-spacing: 2px;
        color: #636d79;
      }
      a {
        text-decoration: underline;
        margin-bottom: 10px;
      }
    }
  }
  .brandLogo {
    &__light {
      display: block;
    }
    &__dark {
      display: none;
    }
    //  Brand logo breackpoint sm
    @include media-breakpoint-down(md) {
      &__light {
        display: block !important;
        max-height: 25px;
      }
      &__dark {
        display: none !important;
      }
    }
  }
  &--show {
    transform: translateY(0);
  }
  &--hidden {
    transform: translateY(-85px);
    @include media-breakpoint-down(md) {
      transform: translateY(-45px);
    }
  }
  &--navOpen {
    transform: translateY(0) !important;
  }
  &--top {
    background: transparent;
    .mainNav {
      &__item {
        a {
          color: $white;
        }
      }
      &__sub {
        background: $white;
        li {
          a {
            color: $dark-gray;
            &:hover {
              color: $primary;
            }
          }
        }
      }
    }
    .brandLogo {
      &__light {
        display: none;
      }
      &__dark {
        display: block;
      }
    }
  }
  // header breakpoint sm
  @include media-breakpoint-down(md) {
    background: $white !important;
    &__row {
      padding-top: 10px;
      padding-bottom: 10px;
      align-items: center;
      justify-content: center;
    }
  }
}