//puppies page
.puppies {
  &__heading {
    @include fontSize(44px);
    font-family: $gotham;
    font-weight: 700;
    line-height: 0.88;
    letter-spacing: 9.4px;
    color: $dark-gray;
    margin-bottom: 20px;
  }

  &__bar {
    justify-content: space-between;
  }

  &__card {
    &:hover {
      .video-available-img {
        transform: scale(1.07);
      }
    }
  }

  &__breed-name {
    @include fontSize(12px);
    font-family: $lato;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.9px;
    color: #898989;
  }

  &__info {
    padding: 4px 0;
    border-bottom: 1px dotted $dark-gray;

    ul {
      margin: 0;
      padding: 0;
    }

    li {
      display: inline-block;
      @include fontSize(12px);
      font-family: $lato;
      font-weight: 300;
      letter-spacing: 1.5px;
      text-transform: uppercase;
      color: $dark-gray;
    }
  }

  &__title {
    h6 {
      letter-spacing: 1.4px;
      @include fontSize(18px);
      text-transform: uppercase;
      margin: 0;
      padding: 8px 0;
      color: $dark-gray;
      font-weight: 500;

      span {
        font-weight: 900;
      }
    }
  }

  &__city {
    background: $primary;
    display: inline-flex;
    align-items: center;
    padding: 4px 7px;
    border-radius: 3px;

    .fa {
      color: $white;
      margin-right: 4px;
      font-size: 10px;
    }

    p {
      font-family: $gotham;
      @include fontSize(12px);
      font-weight: 600;
      line-height: 1;
      letter-spacing: 0.4px;
      margin: 0;
      color: $white;
    }
  }

  &__similar {
    display: block;

    .puppies__info {
      border-bottom: 1px dotted #45515d;

      li {
        font-family: 'Gotham-Light', sans-serif;
        @include fontSize(10px);
      }
    }

    .puppies__title {
      margin-bottom: 5px;
    }
  }

  @include media-breakpoint-down(md) {
    &__similar {
      display: block;
      margin-bottom: 65px;

      .small-img {
        max-width: 100%;
        height: auto;
      }

      .puppies__image {
        padding-top: 0;
      }
    }
    &__heading {
      @include fontSize(22px);
    }
    &__list {
      margin: 0 auto;
      padding-top: 120px;
    }
    &__bar {
      padding: 0;
    }
    .filters {
      margin-top: 0;

      &__bar {
        flex-wrap: wrap;
        position: absolute;
        z-index: 99;

        h6 {
          letter-spacing: 3.5px;
        }

        &--fixed {
          position: fixed;
          top: 0;
        }
      }

      &__info {
        margin-right: 0;
        flex: 1 1 100%;
        text-align: center;
        padding: 15px 0;
        background: #2d3741;
      }

      &__actions {
        width: 25%;

        > label {
          display: block;
          padding: 12px 0 18px;
          margin: 0;
          text-align: center;
          background: transparent;
          transition: all ease-in-out 250ms;
        }

        > .btn {
          display: block;
          bottom: 0;
          background: transparent;
          color: transparent;
          position: absolute;
          height: 45px;
          max-width: 25%;
          overflow: hidden;
          border: none;
          min-width: initial;

          .fa {
            color: #ffffff;
            position: absolute;
            bottom: 4px;
            left: calc(50% - 6px);
            transition: all ease-in-out 250ms;
          }
        }

        button.collapsed ~ label {
          background: #000;
        }

        &--show {
          > label {
            background: $white;
            color: $dark-gray;
          }

          > .btn {
            .fa {
              color: $dark-gray;
              transform: rotate(180deg);
            }
          }
        }
      }
    }
  }
  @include media-breakpoint-down(sm) {
    &__card {
      margin-left: auto;
      margin-right: auto;
    }
    .filters {
      &__box {
        height: calc(100vh - 99px);
        max-height: initial;
      }

      &__apply {
        .btn {
          padding: 15px 0;
        }
      }

      &.show {
        .filters__apply {
          position: fixed;
          width: 100%;
          bottom: 0;
        }
      }
    }
  }
}

.puppy-card {
  position: relative;
  background: $white;
  height: 100%;
  box-shadow: 0 2px 4px 0 rgba(204, 204, 204, 0.5);

  &,
  &__content {
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex: 1;
    padding: calcRem(13px) calcRem(15px);
  }

  &__media {
    aspect-ratio: 1 / 1;
    display: block;
    max-width: 100%;
    overflow: hidden;
    position: relative;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__name,
  &__breed {
    font-weight: 900;
    text-transform: uppercase;
  }

  &__name {
    font-size: clamp(19px, 1.5vw, 22px);
    margin: 0;
    color: $primary;
  }

  &__breed {
    font-size: 16px;
    color: $new-dark-gray;
    font-weight: 900;
    line-height: 1;
    margin-bottom: 12px;
    text-transform: uppercase;
  }

  &__features {
    border-top: 1px dashed $new-dark-gray ;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 12px;
    list-style: none;
    padding-block: 12px;
    padding-inline: 0;

    li {
      color: $new-dark-gray;
      font-size: 8px;
      text-transform: uppercase;

      b {
        display: block;
        font-size: 11px;
      }
    }
  }

  &__action {
    border-radius: 0;
    padding-block: 10px;
  }
}