.birds {
    &__card {
        margin-bottom: calcRem(40px);
    }

    &__image {
        img {
            display: block;
            width: 100%;
            max-width: 100%;
        }
    }

    &__title {
        background: $white;
        padding: calcRem(23px) calcRem(30px);

        h4 {
            @include fontSize(18px);
            color: $dark-gray;
            text-transform: uppercase;
            line-height: 1.94;
            letter-spacing: 3px;
            margin-bottom: 0;
        }
    }

    @include media-breakpoint-down(md) {
        margin-top: 30px;
    }
}
