.facetime {
    background: #f1f1f1;
    &__title {
        display: flex;
        align-items: center;
        justify-content: center;

        h1, h2 {
            text-transform: uppercase;
            font-size: 1.8rem;
            color: $primary;
            margin-bottom: 0;
            text-align: center;
        }

        h1 {
            text-align: left;
        }

        h2 {
            color: $dark-gray;
        }

        img {
            width: 80px;
            height: auto;
            margin-right: 15px;
        }

    }

    &__head {
        background: $white;
        box-shadow: 0 2px 4px 0 rgba(206, 206, 206, 0.5);
    }

    .close {
        font-size: 4rem;
        font-weight: 100;
    }

    &__list {
        list-style: none;
        max-width: fit-content;
        margin: 0 auto;
        padding-left: 0;

        li {
            color: $dark-gray;
            font-family: $lato;
            padding: 0;
            display: flex;
            align-items: center;
            margin: 20px 0;
            font-size: 1rem;
            font-weight: 300;
            line-height: 2.5;
            letter-spacing: 0.4px;
            text-align: center;

            &:last-child {
                margin-bottom: 0;
            }
        }

        span {
            font-size: 1.5rem;
            font-weight: 900;
            width: 30px;
            height: 30px;
            text-align: center;
            line-height: 25px;
            border: 2px solid $primary;
            border-radius: 50%;
            color: $primary;
            margin-right: 20px;
        }
    }

    @include media-breakpoint-up(md) {
        &__title {
            h1 {
                font-size: 3.5rem;
            }

            h2 {
                font-size: 3rem;
            }
        }

        &__list {
            max-width: 620px;

            li {
                font-size: 2rem;
            }

            span {
                font-size: 2rem;
                width: 45px;
                height: 45px;
                line-height: 40px;
            }
        }
    }
}