// breeds Page
.breeds {
  &__card {
    background: $white;
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__image {
    overflow: hidden;

    img {
      transform: scale(1);
      width: 100%;
      max-width: 100%;
      height: 320px;
      object-fit: cover;
      object-position: center;
      transition: all ease-in 0.2s;

      &:hover {
        transform: scale(1.07);
      }
    }
  }

  &__title {
    background: $primary;
    padding: calcRem(10px) calcRem(20px);
    flex: 1;

    h6 {
      @include fontSize(18px);
      color: $white;
      margin: 0;
      line-height: 2;
      letter-spacing: 1.5px;
      text-transform: uppercase;
    }

    a {
      text-decoration: none;
    }
  }

  &__description {
    padding: calcRem(19px) calcRem(19px) calcRem(27px);
    position: relative;

    p {
      margin: 0;
    }

    a {
      position: absolute;
      color: $dark-gray;
      right: calcRem(15px);
      bottom: calcRem(10px);
    }
  }

  @include media-breakpoint-down(md) {
    &__title {
      padding: 0 calcRem(20px);
    }
  }
}