/**
 * Modal
 */
.modal {
    &--extended {
        padding-right: 0 !important;

        .modal {
            &-dialog {
                max-width: 100%;
                margin: 0;
                width: 100vw;
                min-height: 100vh;
            }
        }
    }

    &-dialog-fluid {
        max-width: 870px;
    }

    &-open {
        .header {
            z-index: 999;
        }
    }

    &-header {
        border-bottom: 0;
    }
}