.thank-you {
    border-top: 3px solid #000000;
    padding-top: 50px;
    margin-bottom: 100px;

    &__content {
        max-width: 90%;
        margin: 0 auto;
    }

    &__description {
        font-family: 'Gotham', sans-serif;
        font-weight: 300;
        font-size: 20px;
        line-height: 1.55;
        letter-spacing: 0.6px;
        margin-bottom: 1.125rem;
    }

    &__social {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        width: 64px;
        height: 64px;
        border-radius: 50%;
        background: $dark;
        text-align: center;
        margin-right: 39px;
        margin-top: 38px;
        -webkit-transition: all ease-in-out 200ms;
        transition: all ease-in-out 200ms;

        .fa {
            font-size: 40px;
            line-height: 64px;
            color: $white;
        }

        img {
            padding: 15px;
            max-width: 100%;
        }

        &:hover {
            background: $primary;
        }
    }
}