.schedule {
  &__title {
    font-family: $gotham;
    font-weight: 700;
    @include fontSize(15px);
    text-transform: uppercase;
    line-height: 1.7;
    letter-spacing: 1.2px;
  }

  &__days {
    display: block;
    @include fontSize(13px);
    line-height: 1.7;
    letter-spacing: 1.2px;
  }

  @include media-breakpoint-down(md) {
    &__title,
    &__days {
      @include fontSize(18px);
      line-height: normal;
      letter-spacing: 1.7px;
    }
  }
}