.event {
    &__card {
        background: $white;
        margin-bottom: 4px;
        padding: calcRem(50px) calcRem(45px) calcRem(40px);
    }

    &__label {
        font-family: $gotham;
        @include fontSize(12px);
        letter-spacing: 4.8px;
        margin-bottom: 8px;
    }

    &__specs {
        h4 {
            @include fontSize(18px);
            line-height: 1.61;
            letter-spacing: 0.9px;
        }
    }

    &__button {
        .btn {
            min-width: 150px;
        }
    }

    &__expand {
        border: solid 1px #c0c0c0;
        padding: calcRem(26px) calcRem(45px);
        margin-bottom: 4px;
    }

    &__head {
        margin-bottom: calcRem(35px);
    }

    &__title {
        @include fontSize(28px);
        line-height: 1.25;
        letter-spacing: 4.8px;
    }

    &__image {
        max-width: 100%;
        width: 100%;
    }

    &__info {
        margin-top: calcRem(20px);
        margin-bottom: calcRem(26px);
        display: grid;
        grid-template-columns: auto 1fr;
        grid-gap: 20px;
        align-items: end;
    }

    &__date {
        text-align: center;

        h2 {
            @include fontSize(37px);
        }

        h4 {
            @include fontSize(19px);
        }
    }

    &__more {
        p {
            font-family: $gotham;
            margin-bottom: 0;
            line-height: 1.81;
            letter-spacing: 0.5px;
        }
    }

    &__description {
        column-count: 2;
        column-gap: 40px;
    }

    @include media-breakpoint-down(md) {
        &__specs {
            margin-bottom: 18px;
        }
        &__description {
            column-count: 1;
        }
        &__label {
            @include fontSize(16px);
        }
        &__button {
            .btn {
                display: block;
                width: 100%;
            }
        }
        &__info {
            grid-template-columns: 100%;
            grid-gap: 20px;
            align-items: end;
        }
        &__date {
            text-align: left;

            h2 {
                @include fontSize(50px);
            }

            h4 {
                @include fontSize(28px);
            }
        }
    }
}