
.supplies {
    &__card {
        margin-bottom: 22px;

        &--right {
            padding-left: 13px;
        }

        &--left {
            padding-right: 13px;
        }
    }

    &__content {
        width: 100%;
        background: $white;
        padding: calcRem(35px) calcRem(50px);
    }

    &__title {
        h4 {
            @include fontSize(28px);
            text-transform: uppercase;
            line-height: 1.1;
            letter-spacing: 5.1px;
            word-spacing: 200px;
        }
    }

    &__btn {
        text-align: right;

        .btn {
            min-width: 270px;
            font-family: $gotham;
            font-weight: 400;
            font-size: calcRem(23px);
            padding: calcRem(16px) calcRem(18px);

            .fa {
                margin-left: 20px;
                font-size: 1.3rem;
            }

            &.collapsed {
                background: $primary;

                .less {
                    display: none;
                }

                .more {
                    display: inherit;
                }

                .fa {
                    transform: rotate(180deg);
                }
            }
        }
    }

    &__info {
        padding-top: 20px;

        h6,
        p {
            padding-top: 14px;
            font-size: 16px;
            line-height: 1.5;
            color: $dark-gray;
        }

        p {
            padding-top: 6px;
        }
    }

    @include media-breakpoint-down(md) {
        &__image {
            max-width: 85%;
        }
        &__card {
            padding: 0;

            &--right {
            }

            &--left {
                .supplies {
                    &__image {
                        margin-left: auto;
                    }

                    &__btn {
                        text-align: left;
                    }
                }
            }
        }
        &__title {
            h4 {
                @include fontSize(28px);
            }
        }
    }
}