.article {
    padding: 0 30px 60px 60px;

    &__content {
        max-height: 65vh;
        overflow-y: auto;
        padding-right: 30px;

        &::-webkit-scrollbar {
            width: 6px !important;
            height: 6px !important;
        }

        &::-webkit-scrollbar-thumb {
            background-color: $primary;
        }

        &::-webkit-scrollbar-track {
            background: #d8d8d8;
        }

        h6,
        p {
            font-size: 1rem;
            font-family: $gotham;
            color: $dark;
            line-height: 1.8;
            letter-spacing: 0.5px;
        }

        p {
            font-weight: 300;
        }
    }

    &__title {
        @include fontSize(35px);
        font-family: $gotham;
        color: $dark;
        max-width: 50%;
        letter-spacing: 1.8px;
        margin-bottom: calcRem(35px);
    }

    &__video {
        iframe {
            width: 100%;
            height: 400px;
        }
    }

    @include media-breakpoint-down(md) {
        padding: 0 10px 20px 20px;
        &__content {
            max-height: 60vh;
            padding-right: 10px;
        }
        &__title {
            @include fontSize(25px);
            max-width: 100%;
        }
        &__video {
            iframe {
                height: 300px;
            }
        }
    }
}