.why-petland {
  @include media-breakpoint-up(lg) {
    border-bottom: 1px solid $new-dark-gray;
    margin-bottom: 24px;
    padding-bottom: 24px;

    .available-puppies & {
      display: none;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    text-align: center;
  }

  &__title-item {
    letter-spacing: 1.4px;
    font-size: 1.125rem;
    line-height: 1.67;
    margin-bottom: 0;
    margin-top: 2rem;
    text-transform: uppercase;

    .mobile-modal & {
      font-size: 12px;
      @include media-breakpoint-down(md) {
        br {
          display: none;
        }
      }
    }
  }

  &__image {

    max-width: 70%;
    margin: 0 auto;
    .mobile-modal & {
      max-width: 60px;
    }
  }

  @include media-breakpoint-down(md) {
    &__row {
      flex-wrap: wrap;
    }

    &__col {
      flex: 1 1 50%;
      margin-bottom: 24px;
      padding: 10px;
      width: 50%;
    }
  }
}

.benefit-card {
  background: $white;
  height: 100%;
  box-shadow: 0 2px 4px 0 rgba(204, 204, 204, 0.5);
  min-height: 410px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}