.solutions-pet-text {
    p {
        font-family: $gotham;
        line-height: 1.81;
        letter-spacing: 0.5px;
        margin-bottom: calcRem(18px);
    }

    hr {
        margin-bottom: calcRem(36px);
    }
}

.credit-card-logos {
    text-align: left;
    list-style: none;
    margin: 0;
    padding: 1em 0;

    li {
        display: inline-block;
        margin: 0 5px;
    }
}

.reserve-playtime {
    text-transform: uppercase;
    text-align: center;
    font-family: $lato;
    font-weight: bold;
    color: $dark-gray;
    padding-bottom: 7px;
    font-size: calcRem(12px);
    letter-spacing: 2px;
    margin-top: 15px;

    @include media-breakpoint-down(md) {
        margin-bottom: 25px;
        margin-top: 0;
    }
}

// Available Puppies Carousel
.dot {
    background: $primary !important;
}

.youtube-video {
    width: 100% !important;
    height: 100% !important;

    & > img {
        display: none !important;
    }
}

.thumbs {
    margin-bottom: 0;
    padding: 0;
}

.thumb {
    &.selected {
        border: solid 3px $primary !important;
    }
}

.control-arrow {
    &.control-prev::before {
        border-right: 8px solid $primary !important;
    }

    &.control-next::before {
        border-left: 8px solid $primary !important;
    }
}

.thumbs.yt :first-child::after {
    content: '';
    position: absolute;
    left: 23px;
    top: 20px;
    width: 35px;
    height: 45px;
    opacity: 0.9;
    background-image: url(../assets/images/play-btn-yt.png);
    background-size: 35px;
    background-repeat: no-repeat;
}

.pl-checkbox {
    position: relative;
    display: block !important;
    margin-left: 5px;
    font-size: 0.9rem;
    line-height: 2.21;
    letter-spacing: 0.4px;
    padding-left: 35px;

    .checkmark {
        position: absolute;
        top: 5px;
        left: 0;
        height: 23px;
        width: 23px;
        background-color: $primary;
        border: 1px solid $secondary;
        border-radius: 4px;

        &:after {
            content: '';
            position: absolute;
            display: none;
            left: 9px;
            top: 5px;
            width: 5px;
            height: 10px;
            border: solid $white;
            border-width: 0 3px 3px 0;
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
        }
    }

    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;

        &:checked {
            ~ .checkmark {
                background-color: $primary;

                &:after {
                    display: block;
                }
            }
        }
    }
}

.is-invalid {
    display: block;
    font-size: 0.8rem;
    color: #ab3a1b;
}

.adopted-puppy-message {
    font-size: 30px;
    background: #fd7e14;
    text-align: center;
    color: white;
    font-family: 'Gotham', sans-serif;
}

.video-available-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 0, 0, 0.9);
    position: absolute;
    width: 50px;
    height: 50px;
    top: 204px;
    right: 7px;
    z-index: 1;
    border-radius: 6px;
}

.video-available-img {
    width: 28px !important;
    height: 22px !important;
    margin: 5px 6px 0 6px;
    transform: scale(1);
    max-width: 100%;
    object-fit: cover;
    object-position: center;
    @include transition(all ease-in 0.3s);
}

.video-available-label {
    font-weight: 500;
    color: white;
    font-size: 9px;
    letter-spacing: 0.96px;
    margin-bottom: 2px;
    margin-left: 2px;
}

.wp-block-columns {
    display: flex;
    flex-wrap: wrap
}

@media (min-width: 782px) {
    .wp-block-columns {
        flex-wrap:nowrap
    }
}

.wp-block-column {
    flex: 1;
    flex-basis: 100%;
    margin-bottom: 1em;
    min-width: 0;
    overflow-wrap: break-word;
    word-break: break-word
}

@media (min-width: 600px) {
    .wp-block-column {
        flex-basis:50%;
        flex-grow: 0
    }
}

@media (min-width: 600px) {
    .wp-block-column:nth-child(odd) {
        margin-right:32px
    }

    .wp-block-column:not(:first-child),.wp-block-column:nth-child(2n) {
        margin-left: 32px
    }

    .wp-block-column:not(:last-child) {
        margin-right: 32px
    }
}

.wp-block-image {
    margin-bottom: 1em;
    margin-left: 0;
    margin-right: 0;
    max-width: 100%
}

.wp-block-image img {
    max-width: 100%
}

.wp-block-image.aligncenter {
    text-align: center
}

.wp-block-image.alignfull img,.wp-block-image.alignwide img {
    width: 100%
}

.wp-block-image .aligncenter,.wp-block-image .alignleft,.wp-block-image .alignright,.wp-block-image.is-resized {
    display: table;
    margin-left: 0;
    margin-right: 0
}

.wp-block-image .aligncenter>figcaption,.wp-block-image .alignleft>figcaption,.wp-block-image .alignright>figcaption,.wp-block-image.is-resized>figcaption {
    caption-side: bottom;
    display: table-caption
}

.wp-block-image .alignleft {
    float: left;
    margin-right: 1em
}

.wp-block-image .alignright {
    float: right;
    margin-left: 1em
}

.wp-block-image .aligncenter {
    margin-left: auto;
    margin-right: auto
}

.wp-block-image figcaption {
    color: #555d66;
    font-size: 13px;
    margin-bottom: 1em;
    margin-top: .5em;
    text-align: center
}