
.cares {
    section {
        margin-bottom: 30px;
    }

    &__slide {
        height: fit-content;
    }

    &__card {
        position: relative;
    }

    &__image {
        img {
            display: block;
            width: 100%;
        }
    }

    &__button {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 9;
        background: $dark-gray;
        color: $white;
        width: 71px;
        height: 71px;
        border: 0;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}