// location Info
.location {
  &__group {
    margin-bottom: 1.2rem;
  }

  &__city {
    font-family: $gotham;
    font-weight: 700;
    @include fontSize(15px);
    text-transform: uppercase;
    line-height: 1.7;
    letter-spacing: 1.2px;

    img {
      width: calcRem(13px);
      margin-right: 0px;
    }
  }

  &__address,
  &__hours {
    @include fontSize(12px);
    line-height: 1.75;
    letter-spacing: 1.2px;
    padding-left: 30px;
    padding-right: calcRem(25px);
    margin-bottom: calcRem(5px);
  }

  &__hours {
    @include fontSize(10px);
    padding: 5px 0;
    /*display: flex;*/
    align-items: flex-start;
    border-top: 2px solid #979797;

    img {
      margin-right: 5px;
      /*margin-top: 5px;*/
      width: 12px;
      height: 12px;
    }
  }

  @include media-breakpoint-down(md) {
    &__city,
    &__address,
    &__hours {
      font-family: $gotham;
      line-height: normal;
      letter-spacing: 1.7px;
      padding: 0;

      img {
        display: none;
      }
    }
    &__address,
    &__hours {
      font-family: 'Gotham-Light', sans-serif;
      @include fontSize(17.5px);
      font-weight: 300;
      border-top: none;
    }
    &__city {
      @include fontSize(18px);
      margin-bottom: 30px;
      font-weight: 700;
    }
    &__hours {
      margin-top: 0.8rem;

      img {
        display: inline-block;
        vertical-align: top;
      }
    }
  }
}