// Masthead Home
.masthead {
  position: relative;
  &__picture {
    display: block;
    overflow: hidden;
    &--fluid {
      max-height: 780px;
    }
    img {
      max-width: 100%;
      width: 100%;
    }
  }
  &--cut {
    height: 180px;
    overflow: hidden;
  }
  &__content {
    position: absolute;
    top: 30%;
    left: 58%;
    max-width: 265px;
    text-align: left;
    overflow: hidden;
    &--internal {
      .solutions-pet & {
        padding-bottom: 0;
      }
      position: relative;
      width: 100%;
      padding-top: calcRem(42px);
      padding-bottom: calcRem(42px);
      .masthead__title,
      .masthead__text {
        color: $dark-gray;
        word-spacing: normal;
      }
      .masthead__title {
        @include fontSize(44px);
        line-height: 1;
        display: block;
        align-self: center;
        overflow: hidden;
        small {
          @include fontSize(17px);
          letter-spacing: calcRem(6px);
          display: block;
          width: max-content;
          font-weight: bold;
        }
        .solutions-pet & {
          padding-left: 0 !important;
        }
      }
      .masthead__text {
        @include fontSize(20px);
        &--fsm {
          @include fontSize(16px);
        }
      }
    }
  }
  &__title {
    @include fontSize(35px);
    text-transform: uppercase;
    color: $white;
    word-spacing: 100vw;
    letter-spacing: calcRem(7px);
    line-height: 1.17;
  }
  &__text {
    @include fontSize(18px);
    color: $white;
    line-height: 1.61;
    letter-spacing: 0.5px;
  }
  &__row {
    display: grid;
    grid-template-columns: 30% 35% 35%;
    grid-gap: 15px;
    &--fluid {
      grid-template-columns: 100%;
      .masthead__title {
        max-width: 100% !important;
      }
    }
  }
  @include media-breakpoint-up(md) {
    .collapse {
      display: block;
    }
  }
  @include media-breakpoint-down(md) {
    &__content {
      top: 20px;
      left: 0;
      max-width: 100%;
      text-align: center;
      &--internal {
        position: absolute;
        width: 100%;
        bottom: 0;
        padding-top: 0;
        padding-bottom: calcRem(25px);
        .masthead__title,
        .masthead__text {
          color: $white;
        }
        .masthead__title {
          max-width: 70%;
          margin-left: auto;
          overflow: hidden;
          @include fontSize(28px);
          line-height: 1.39;
          letter-spacing: calcRem(12.5px);
          small {
            @include fontSize(28px);
            font-weight: 900;
          }
        }
        .masthead__text {
          margin-top: 15px;
        }
      }
      &--bg-text {
        position: relative;
        padding-left: 0;
        padding-bottom: 0;
        .masthead {
          &__title {
            text-align: left;
            line-height: 1.2;
            margin: 0;
            padding-bottom: 30px;
            span,
            small {
              background: $white;
              color: $dark-gray;
              padding-left: 25px;
              padding-right: 5px;
              letter-spacing: 5.5px;
            }
          }
          &__text {
            color: $dark-gray;
          }
        }
      }
    }
    &__title {
      @include fontSize(28px);
      letter-spacing: calcRem(12px);
      line-height: 1.4;
    }
    &__text {
      line-height: 1.3;
      max-width: 80%;
      margin: 50% auto 15px;
    }
    &__row {
      display: block;
    }
  }
  @include media-breakpoint-between(md, lg) {
    &__text {
      margin-top: 60px;
    }
  }
  @media only screen and (max-width: 320px) {
    &__text {
      margin-top: 100px;
    }
  }
}