.extras {
    &__title {
        font-size: calcRem(26px);
        line-height: 1.4;
        letter-spacing: 3.7px;
        padding: 0 calcRem(38px) calcRem(21px);
        margin: 0;
        color: #404041;
        max-width: 340px;
        text-transform: uppercase;
    }

    &__subtitle {
        @include fontSize(18px);
        letter-spacing: 3.1px;
        color: #404041;
        text-transform: uppercase;
    }

    &__image {
        margin-bottom: 25px;
        overflow: hidden;

        img {
            max-width: 100%;
            width: 100%;
            height: 170px;
            object-fit: cover;
            object-position: center;
        }
    }

    &__content {
        overflow: hidden;

        h4 {
            @include fontSize(18px);
            line-height: 2;
            letter-spacing: 3px;
            color: #404041;
            text-transform: uppercase;
        }

        p {
            font-family: $gotham;
            font-weight: 300;
            line-height: 1.81;
            letter-spacing: 0.5px;
        }

        .btn {
            margin: 29px 0;

            .fa-angle-right {
                margin-left: 20px;
            }

            .fa-angle-left {
                margin-right: 20px;
            }
        }
    }

    &__swiper {
        background: $white;
        padding: calcRem(25px) calcRem(30px);

        ul {
            list-style: none;
            margin: 0;
            padding-left: 0;
            padding-right: calcRem(35px);
        }

        li {
            font-family: $gotham;
            font-weight: 300;
            line-height: 1.81;
            letter-spacing: 0.5px;
            margin-bottom: 15px;
        }
    }

    &__card {
        border-radius: 10px;
        background: $white;
        padding: 30px 0;
        margin-bottom: 25px;
        height: fit-content;

        &--dark {
            background: $dark-gray;

            .extras {
                &__title,
                &__subtitle {
                    color: $white;
                }
            }

            .swiper-slide {
                background: $white;
                height: auto;
            }
        }
    }

    &__content {
        padding: 0 calcRem(40px);

        .about__list {
            margin: calcRem(40px) 0;
        }
    }

    .btn--collapse {
        padding: calcRem(8px) calcRem(30px);
        font-family: $lato;
        display: flex;
        margin-bottom: 2px;
        justify-content: space-between;
        background: $dark-gray;
        color: $white;
        font-size: calcRem(16px);
        letter-spacing: 2.5px;
        text-transform: uppercase;

        .fa {
        }

        &.collapsed {
            color: rgba(255, 255, 255, 0.5);

            .fa {
                transform: rotate(-90deg);
            }
        }
    }

    @include media-breakpoint-down(md) {
        &__card {
            &--fluid {
                margin-left: -15px;
                margin-right: -15px;
                border-radius: 0;
            }
        }
        &__swiper {
            ul {
                padding: 0;
            }
        }
        &__content {
            padding: 0 calcRem(30px);

            &--pos {
                padding-top: 6rem;
                margin-top: -6rem;
                background: #f1f1f1;
            }
        }
        &__image {
            img {
                max-width: 93%;
                object-position: left;
            }
        }
    }
}