.contact {
    &__info {
        background: $dark-gray;
        border-radius: 8px;
        padding: calcRem(26px) calcRem(36px);
    }

    &__item {
        margin-bottom: 15px;

        h4 {
            font-family: $gotham;
            font-weight: 700;
            @include fontSize(27px);
            letter-spacing: -0.4px;
            text-decoration: underline;
            color: $white;
        }

        p {
            font-family: $gotham;
            @include fontSize(16px);
            color: $white;
            margin-bottom: 0;
        }
    }

    &__location {
        padding: 30px 0;
        margin-bottom: 50px;
    }

    .location {
        &__group {
            margin-bottom: 30px;
        }

        &__address,
        &__city,
        &__time {
            font-size: calcRem(20px);
        }

        &__city {
            img {
                width: 1.8rem;
            }
        }

        &__address {
            padding-left: 2.5rem;
        }

        &__time {
            display: flex;
            letter-spacing: 1.2px;
            align-items: flex-start;
            font-weight: 300;
            line-height: 1.6;

            img {
                width: 20px;
                margin-right: 18px;
                margin-top: 7px;
            }
        }
    }
}