
.career {
    margin: 42px 0;

    &__drop {
        position: absolute;
        width: 100%;
        height: 100%;
        cursor: pointer;
        top: 0;
        left: 0;
    }

    &__expand {
        padding: calcRem(40px);
        background: $white;
        margin-top: 7px;
    }

    &__description {
        margin-bottom: 1.5rem;

        .career__compress {
            display: grid;
            grid-gap: 20px;
            margin-bottom: 0.5rem;

            .btn {
                justify-self: end;
            }
        }
    }
}