//Filters
.filters {
  @include media-breakpoint-up(lg) {
    position: sticky;
    align-self: start;
    top: 2rem;
    z-index: 1;
    transition: all ease-in-out 0.3s;

    .header-showed & {
      top: 6rem;
    }
  }

  &__box {
    background: $white;
    padding: calcRem(20px) calcRem(15px);
    max-height: 400px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 6px !important;
      height: 6px !important;
    }

    &::-webkit-scrollbar-thumb {
      background-color: rgba(0, 0, 0, 0.2);
    }

    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.08);
    }
  }

  &__item {
    margin-bottom: 17px;

    &:last-child {
      padding-bottom: 0;
    }
  }

  &__bar {
    display: grid;
    left: 50%;
    position: absolute;
    width: 100%;
    transform: translate(-50%, 0);

    .btn {
      font-size: 14px;
      height: 58px;
      line-height: 1.2;
      min-width: auto;
      padding: 0 12px;
      font-weight: 700;
      transition: all ease-in-out 300ms;

      .icon {
        margin-right: 8px;
      }
    }

    &:not(&--fixed) {
      gap: 20px;

      &::after {
        display: none;
      }
    }

    &--2-col {
      grid-template-columns: 1fr 1fr;

      &::after {
        background: $white;
        content: '';
        left: 50%;
        height: 14px;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 1px;
      }

    }

    &--fixed {
      max-width: 100%;
      padding: 0;
      position: fixed;
      top: 0;
      transition: all ease-in-out 300ms;
      z-index: 999;

      .btn {
        height: 50px;
      }

      .header-showed & {
        transform: translate(-50%, 45px);
      }
    }
  }

  &__head {
    @include media-breakpoint-up(md) {
      margin-bottom: 42px;
    }
  }

  &__title {
    color: $new-dark-gray;
    font-size: 38px;
    line-height: 1;
    margin-bottom: 18px;
    max-width: 200px;
    text-transform: uppercase;
  }

  &__text {
    line-height: 1.375;
  }

  &__actions {
    > label {
      @include fontSize(10px);
      font-family: $lato;
      text-transform: uppercase;
      letter-spacing: 0.7px;
      color: $white;
      margin-right: 5px;
    }
  }

  .filter-group-title {
    border-bottom: 1px solid $light-grey;
    color: $new-dark-gray;
    font-size: 21px;
    line-height: 1.24;
    margin-bottom: 17px;
    padding: 16px;
    text-transform: uppercase;
  }
}

.filter {
  &__element {
    visibility: visible;
    transform: scaleY(1);
    max-height: 2000px;
    transition: transform ease-out 0.2s, display ease-out 0.1s 1s;

    &.hide {
      visibility: hidden;
      transform: scale(0);
      max-height: 0;
      transition: transform ease-out 0.2s 0.3s;

      * {
        margin: 0 !important;
      }
    }
  }
}