.faq {
    &__card {
        height: auto;
        position: relative;
    }

    &__col {
        position: relative;

        &::before {
            content: 'Q';
            font-family: $gotham;
            font-size: calcRem(243px);
            font-weight: 700;
            position: absolute;
            left: -30px;
            top: -30px;
            line-height: 1;
            color: #51ece7;
        }

        @include media-breakpoint-up(md) {
            &:last-child::before {
                display: none;
            }
        }
    }
}