// main Home
.mainHome {
    display: grid;
    grid-template-columns: 45% 55%;
    grid-column-gap: calcRem(24px);
    align-items: end;
    position: relative;
    top: -35px;

    &__image {
        img {
            max-width: 100%;
        }
    }

    &__title {
        color: $dark-gray;
        @include fontSize(44px);
        text-transform: uppercase;
        line-height: 1.17;
        letter-spacing: calcRem(7.6px);
        max-width: 80%;

        small {
            display: block;
            @include fontSize(17px);
            line-height: 1.58;
            letter-spacing: calcRem(6px);
        }
    }

    &__text {
        position: relative;
        margin-top: 1rem;
        margin-left: calcRem(-44px);
        padding: calcRem(25px) calcRem(45px) calcRem(35px);
        background: rgba(233, 233, 233, 0.88);
        @include fontSize(19px);
        line-height: 1.95;
        letter-spacing: 0.6px;
    }

    &__button {
        text-align: right;
    }

    @include media-breakpoint-down(lg) {
        top: auto;
        padding-top: 50px;
    }

    @include media-breakpoint-up(sm) {
        background-image: none !important;
    }

    @include media-breakpoint-down(sm) {
        display: block;
        background-position: top center;
        background-size: cover;
        padding: 50px 20px;
        &__image {
            display: none;
        }
        &__title {
            @include fontSize(28px);
            line-height: 1.35;
            letter-spacing: calcRem(12px);
            margin-bottom: 100%;
            padding-left: 20px;
            max-width: 100%;

            small {
                @include fontSize(28px);
                line-height: 1.35;
                letter-spacing: calcRem(12px);
            }
        }
        &__text {
            @include fontSize(18px);
            line-height: 1.7;
            margin-left: 0;
        }
        &__button {
            .btn {
                display: block;
                background: transparent;
                border: 1px solid $white;
                padding: 12px;
            }
        }
    }
}