// footer
.footer {
  padding-top: calcRem(60px);
  margin-top: calcRem(30px);
  background: $white;

  .bg-white + & {
    border-top: 1px solid $light-grey;
    margin-top: 0;
  }

  &__row {
    display: flex;
    margin-right: -15px;
    margin-left: -15px;

    &--sb {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__col {
    padding: 0 15px;
    max-width: 70%;

    &--lg {
      padding: 0 15px;
      max-width: 100%;
    }

    &--20 {
      max-width: 30%;
    }
  }

  &__sub {
    padding-top: calcRem(48px);
    padding-bottom: calcRem(48px);
  }

  &__privacy {
    font-size: 10px;
    letter-spacing: calcRem(3.5px);
    margin-bottom: 0;
  }

  .footNav {
    &__title {
      font-size: 1.2rem;
      text-transform: uppercase;
      font-family: $gotham;
      margin-bottom: 1rem;
    }

    &__list {
      margin: 0;
      padding: 0;

      li {
        list-style: none;
        line-height: 1;
        margin-bottom: 1rem;

        a {
          font-family: $gotham;
          text-transform: uppercase;
          font-size: 10px;
          font-weight: 500;
          font-style: normal;
          font-stretch: normal;
          letter-spacing: 0.7px;
        }
      }
    }
  }

  &__canine {
    margin-left: 15px;
    cursor: pointer;

    img {
      max-width: 50px;
    }
  }

  &__modal {
    z-index: 9999;

    .modal-header {
      flex-direction: column-reverse;
      align-items: center;
      justify-content: center;

      > img {
        max-width: 100px;
      }
    }

    .modal-body {
      padding: 1.2rem 3rem;

      p {
        font-size: 14px;
      }
    }
  }

  @include media-breakpoint-down(md) {
    &__row {
      flex-direction: column;

      &--sb {
        justify-content: center;
      }
    }
    &__col {
      max-width: 100%;
    }
    .location {
      &__group {
        max-width: 80%;
        margin: auto;
        text-align: center;
        padding-top: 30px;
        padding-bottom: 30px;

        &:after {
          content: '';
          background: $dark-gray;
          width: 50px;
          height: 3px;
          border-radius: 4px;
          position: absolute;
          left: calc(50% - 25px);
          bottom: 10px;
        }

        &:last-child:after {
          display: none;
        }
      }
    }
    &__sub {
      margin-top: 20px;
      text-align: center;
      background: $dark-gray;
    }
    &__privacy {
      color: $white;
    }
    .footNav {
      text-align: center;
    }
  }
}