// quick links
.quickLinks {
  position: fixed;
  right: 0;
  top: 35%;
  transform: translateY(-50%);
  background: $white;
  padding: calcRem(16px) calcRem(8px);
  z-index: 999;
  box-shadow: 0 -4px 20px rgba(0, 0, 0, 0.3);
  &__list {
    padding: 0;
    margin: 0;
  }
  &__item {
    display: block;
    width: calcRem(25px);
    text-align: center;
    padding-top: calcRem(16px);
    padding-bottom: calcRem(16px);
    a {
      display: block;
    }
    span {
      font-family: $lato;
      font-weight: 900;
      font-size: 9px;
      letter-spacing: 1.3px;
      color: $dark;
      margin-top: 5px;
      display: block;
      text-transform: uppercase;
      @include media-breakpoint-up(lg) {
        display: none;
      }
    }
  }
  @include media-breakpoint-down(md) {
    position: fixed;
    width: 100%;
    right: auto;
    bottom: 0;
    top: auto;
    transform: none;
    padding: calcRem(8px) 0;
    &__list {
      display: flex;
    }
    &__item {
      width: 20%;
      text-align: center;
      padding: 0;
      img {
        max-height: 25px;
      }
      .iconRotate {
        transform: rotate(90deg);
      }
    }
  }
}