// social List
.social {
  padding-top: calcRem(32px);

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: space-evenly;
    list-style: none;
    padding: 0;
  }

  &__item {
    display: block;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 27px;
      height: 27px;
      text-align: center;
      background: $dark;
      border-radius: 50%;
      @include transition(all ease-in 0.2s);
      color: white;

      &:hover {
        background: $primary;
      }

      img {
        max-height: 18px;
      }

      .icon {
        width: 22px;
        height: 16px;
      }
    }

    &:first-child {
      margin-left: 0;
    }
  }
}