.finance {
    &__card {
        position: relative;
        border-radius: 6px;
        background: $white;
        padding: calcRem(25px);
        height: 100%;
    }

    &__image {
        margin-bottom: 20px;

        img {
            width: 100%;
            display: block;
        }
    }

    &__description {
        margin-bottom: 75px;

        ul {
            list-style: none;
            padding: 0;
            text-align: center;
        }

        li {
            font-family: $gotham;
            @include fontSize(14px);
            font-weight: 400;
            line-height: 1.6;
            letter-spacing: 1px;
            padding: 15px 0;
        }
    }

    &__button {
        position: absolute;
        bottom: calcRem(25px);
        left: 0;
        width: 100%;
        padding: 0 calcRem(25px);

        .btn {
            padding: 0.75rem;
            @include media-breakpoint-up(md) {
                &:hover,
                &:focus {
                    background: $primary;
                    border-color: $primary;
                    color: $white;
                }
            }
        }
    }

    @include media-breakpoint-down(md) {
        &__content {
            background: $dark-gray;

            > .row {
                > div {
                    &:not(:last-child) {
                        .finance {
                            &__button {
                                border-bottom: 2px dotted $white;
                            }
                        }
                    }
                }
            }
        }
        &__image {
            border-radius: 15px;
            background: $white;
            padding: 40px 0;

            img {
                width: 100%;
                display: block;
            }
        }
        &__description {
            color: $white;
        }
        &__button {
            padding: 0 0 calcRem(40px);
            bottom: 0;
            position: relative;

            &--last {
                border-bottom: 0;
                margin-bottom: calcRem(40px);
            }

            .btn {
                border-color: $white;
                color: $white;

                &:hover,
                &:focus {
                    background: $white;
                }
            }
        }
        &__card {
            padding-top: calcRem(40px);
            padding-bottom: 0;
            max-width: 80%;
            margin: 0 auto;
            background: transparent;
        }
    }
}