.about {
    margin-bottom: 50px;

    &__title {
        position: relative;
        @include fontSize(26px);
        text-transform: uppercase;
        letter-spacing: 5.5px;
        padding-bottom: 17px;
        margin-bottom: calcRem(36px);

        &:after {
            content: '';
            width: 100%;
            height: 3px;
            background: $dark;
            position: absolute;
            left: 0;
            bottom: 0;
        }
    }

    &__card {
        padding: calcRem(35px);
        height: 100%;

        &--dark {
            border-top-left-radius: 8px;
            border-top-right-radius: 8px;
            background: $dark-gray;
            max-width: 80%;

            .about {
                &__title,
                &__text {
                    color: $white;

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }

    &__text,
    &__item {
        font-family: $gotham;
        line-height: 1.81;
        letter-spacing: 0.5px;
        margin-bottom: calcRem(18px);

        strong {
            font-weight: 700;
        }
    }

    &__subtitle {
        @include fontSize(16px);
        font-family: $gotham;
        font-weight: 700;
        line-height: 1.5;
        letter-spacing: 0.5px;
        margin-bottom: calcRem(18px);
    }

    &__item {
        list-style: none;
        line-height: 1.4;

        &:before {
            content: '\25AA';
            color: $primary;
            display: inline-block;
            width: auto;
            margin-left: -2rem;
            margin-right: 15px;
            font-size: 40px;
            line-height: 0;
            vertical-align: middle;
        }
    }

    @include media-breakpoint-down(md) {
        .bg-md-dark-gray {
            .about {
                &__card {
                    &--dark {
                        max-width: 100%;
                    }
                }

                &__subtitle,
                &__text,
                &__item,
                &__item:before {
                    color: $white;
                }
            }
        }
    }
}