.features {
  padding: calcRem(40px) 0;
  &__row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 4px;
  }
  .feature {
    $box-pad: calcRem(40px);
    position: relative;
    font-size: $box-pad;
    .feature__title {
      bottom: calcRem(25px);
      left: calcRem(40px);
    }
    &__title {
      position: absolute;
      word-spacing: 300px;
      color: $white;
      @include fontSize(30px);
      line-height: 1.3;
      text-transform: uppercase;
      letter-spacing: calcRem(10px);
    }
    &__text {
      @include fontSize(18px);
      line-height: 1.72;
      letter-spacing: 0.5px;
      color: $white;
    }
    &__cta {
      display: flex;
      width: 100%;
      @include fontSize(23px);
      line-height: 1.87;
      letter-spacing: calcRem(2px);
      color: $white;
      font-family: $gotham;
      font-weight: 300;
      justify-content: space-between;
      position: absolute;
      bottom: calcRem(30px);
      max-width: calc(100% - 80px);
    }
    &--hover {
      position: absolute;
      transform: translateX(-100%);
      top: 0;
      padding: calcRem(30px) calcRem(40px);
      background: $primary;
      height: 100%;
      z-index: 99;
      opacity: 0;
      visibility: hidden;
      @include transition(all ease-in-out 0.3s);
      .feature__title {
        position: relative;
      }
    }
  }
  &__col {
    position: relative;
    overflow: hidden;
    &:hover {
      text-decoration: none !important;
      .feature--hover {
        transform: translateX(0);
        opacity: 1;
        visibility: visible;
      }
    }
    &--green {
      .feature {
        &--hover {
          background: $green;
        }
      }
    }
    &--dark {
      .feature {
        &--hover {
          background: $dark-gray;
        }
      }
    }
  }
  @include media-breakpoint-down(md) {
    &__row {
      display: block;
    }
    .feature {
      &--hover {
        position: relative;
        transform: translateX(0);
        top: 0;
        background: transparent;
        opacity: 1;
        visibility: visible;
        padding: 0;
        margin-bottom: 3.5rem;
        .feature__title {
          display: none;
        }
      }
      &__title {
        position: absolute;
        word-spacing: 300px;
        color: $white;
        @include fontSize(30px);
        line-height: 1.3;
        text-transform: uppercase;
        letter-spacing: calcRem(10px);
      }
      &__text {
        @include fontSize(18px);
        padding: calcRem(30px) calcRem(40px);
        margin-bottom: 0;
        line-height: 1.72;
        letter-spacing: 0.5px;
        color: $dark;
      }
      &__cta {
        position: relative;
        bottom: auto;
        background: $primary;
        padding: 12px 26px;
      }
    }
    &__col {
      &--green {
        .feature__text {
          text-align: right;
        }
        .feature__cta {
          background: $green;
          margin-left: auto;
        }
      }
      &--dark {
        .feature__cta {
          background: $dark-gray;
        }
      }
    }
  }
}