.button-toggle {
  margin-bottom: 17px;

  &__options {
    display: flex;
    gap: 9px;
  }

  &__item {
    border: 2px solid $new-dark-gray;
    border-radius: 4px;
    color: $new-dark-gray;
    flex-basis: 0;
    flex-grow: 1;
    flex-wrap: wrap;
    font-family: $gotham;
    height: 51px;
    padding: 0 8px;

    .icon {
      margin-right: 8px;
    }
  }
}