
.info {
  &--fluid {
    .info {
      &__title {
        max-width: 100%;
      }
    }
  }
  &__title {
    font-family: $gotham;
    font-size: calcRem(20px);
    font-weight: 700;
    line-height: 1.35;
    letter-spacing: 0.6px;
    max-width: 225px;
  }
  &__text {
    border-top: 3px solid $dark;
    padding-top: calcRem(25px);
    font-family: $gotham;
    font-weight: 300;
    line-height: 1.81;
    letter-spacing: 0.5px;
  }
}