.heading {
    &__card {
        position: relative;
        width: 100%;
        height: calcRem(90px);
        display: flex;
        background: $white;
        margin-top: 10px;
        align-items: center;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        width: 100%;
        padding: 0 calcRem(43px);

        &--row {
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
        }
    }

    &__bar {
        height: 100%;
        background: $primary;
        width: 14px;
    }

    &__title,
    &__text {
        font-family: $gotham;
        font-size: calcRem(20px);
        line-height: 1.45;
        letter-spacing: 1px;
        color: $dark;
        margin: 0;
    }

    &__text {
        font-weight: 300;
    }

    &__icon {
        color: #8e8e8e;

        .fa {
            font-size: 1.2rem;
        }
    }
}