/**
 * Swiper
 */
.swiper-button-disabled {
    background: $dark;
    border-color: $dark;
    cursor: none;
}

.swiper--buttons {
    display: flex;
    margin-left: -1px;
    margin-right: -1px;

    .btn {
        margin: 0 1px;
        width: 50%;
        min-width: inherit;

        &:hover {
            background: $dark;
        }
    }
}

.swiper-extras {
    @include media-breakpoint-down(md) {
        max-width: 100%;
        overflow: initial;
        .swiper-slide {
            max-width: 100%;
        }
    }
}

.swiper-only-mobile {
    .swiper-slide {
        width: 85%;
    }

    @include media-breakpoint-up(md) {
        &.swiper-container {
            margin-left: -6px;
            margin-right: -6px;
        }
        .swiper-slide {
            width: 25%;
            padding: 0 6px;
        }
    }
}
