.dropdown {
  position: relative;
  width: 100%;

  &__button {
    align-items: center;
    background: $white;
    border: 1px solid $light-grey;
    border-radius: 4px;
    color: $new-dark-gray;
    display: flex;
    font-family: $gotham;
    height: 54px;
    justify-content: space-between;
    line-height: 1.375;
    padding: 16px;
    width: 100%;

    > i {
      background: $new-dark-gray;
      border-radius: 2px;
      color: $white;
      height: 18px;
      line-height: 18px;
      width: 18px;
    }
  }

  &__wrapper {
    background: $white;
    margin-top: 2px;
    position: relative;

    @include media-breakpoint-up(lg) {
      margin-top: 4px;
    }
  }

  &__options {
    max-height: 324px;
    overflow: auto;
    &::-webkit-scrollbar {
      width: 6px !important;
      height: 6px !important;
    }

    &::-webkit-scrollbar-thumb {
      background-color: rgba(0, 0, 0, 0.2);
    }

    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.08);
    }
  }

  &__option {
    border-bottom: 1px solid $light;

    > label {
      align-items: center;
      display: flex;
      justify-content: space-between;
      height: 54px;
      margin-bottom: 0;
      padding: 0 16px;

      /**
        * TODO Remove it once the dropdown will bee universal in all the filter
       */
      font-weight: 400;
      font-size: 1rem;

      &:before {
        display: none;
      }
    }
  }

  &__action {
    width: 100%;

    @include media-breakpoint-down(lg) {
      display: none;
    }

    .btn {
      align-items: center;
      display: flex;
      font-size: 22px;
      justify-content: space-between;
      height: 60px;
      padding: 0 16px;
      width: 100%;
    }
  }
}

.checked-list {
  list-style: none;
  padding: 0;

  &__item {
    margin-top: 10px;
  }

  &__button {
    align-items: center;
    border: 0;
    background: #e5e5e5;
    display: flex;
    font-family: $gotham;
    justify-content: space-between;
    transition: opacity ease-in-out 250ms;
    width: 100%;

    &:hover {
      opacity: 0.6;
    }
  }
}
