.mobile-modal {
  @include media-breakpoint-down(md) {
    background: $light;
    box-shadow: 4px 1px 20px 10px rgba(0, 0, 0, .40);
    display: flex;
    flex-direction: column;
    left: 0;
    max-height: 100vh;
    max-width: 500px;
    padding: 16px 17px 23px;
    overflow: auto;
    position: fixed;
    top: 0;
    bottom: 0;
    transform: translateX(-100%);
    transition: all ease-in-out 150ms;
    visibility: hidden;
    width: 100%;
    z-index: 9999;

    &__head {
      align-items: center;
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;

      .close-modal {
        background: transparent;
        border: 0;
      }
    }

    &__title {
      color: $new-dark-gray;
      font-size: 38px;
      line-height: 1;
      margin-bottom: 25px;
    }

    &__action {
      margin-top: 2rem;

      .btn {
        width: 100%;
      }
    }

    &--open {
      transform: translateX(0);
      visibility: visible;
    }
  }
}